*{
  margin: 0;
  padding: 0;
}
*,
*:before,
*::after{
  box-sizing:inherit;
}

html{
  box-sizing:border-box;
  font-size:62.5%;
}

.App{
  /* display: flex; */
  flex-direction: column;
  width: 69%;
  font-size: 2rem;
  margin: auto;
  margin-top: 5rem;
}

.filter{
  margin: 2rem 0;
  padding: 1rem;
  background-color:whitesmoke;
  border: 1px solid black;
  border-radius: 5px;
}

.save{
  padding: 1rem 2rem;
  background-color:antiquewhite;
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 12px gray;
}

input{
  padding: 1rem;
  font-size: 2rem;
  width: 100%;
}

.input-notes{
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  margin-top: 2rem;
  /* background-color: yellowgreen; */
  width: 80%;
  align-items: flex-end;
  /* align-items: center; */
}

h1{
  font-size: 6rem;
  background-color: antiquewhite;
  text-align: center;
  width: 80%;
}

.save,.filter:hover{
  background-color: rgb(255, 180, 82);
}

.save{
  width: 10rem;
  margin-top: 2rem;
}


button{
  font-size: 1.8rem;
  
}

ul li{
  margin-top: 1rem;
  padding: .4rem;
  background-color: cornsilk;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
}

.notImportant{
  border: 1px solid black;
  border-radius: 5px;
  padding: .4rem;
  margin-left: .6rem;
  cursor: pointer;
}

.notImportant:hover{
  background-color: crimson;
  color: aliceblue;
}


.important{
  border: 1px solid black;
  border-radius: 5px;
  padding: .4rem;
  margin-left: .6rem;
  background-color: crimson;
  color: whitesmoke;
  cursor: pointer;
}

.important:hover{
  background-color: whitesmoke;
  color: black;
}


.container{
  /* background-color: yellow; */
  /* display: flex; */
  /* width: 80%; */
  margin: auto;
  flex-direction: column;
}